<template>
  <div class="_page">
    <div id="case">
      <!-- å·¥å…·æ  -->
      <div class="toolbar">
        <!-- æœç´¢æ¡† -->
        <div class="search-box">
          <input
            type="text"
            v-model="searchKeyword"
            placeholder="æœç´¢æ¡ˆä¾‹åç§°æˆ–ä»‹ç»..."
            class="search-input"
          />
          <span class="search-icon">ğŸ”</span>
        </div>

        <!-- ç»Ÿè®¡ä¿¡æ¯ -->
        <div class="stats">
          <span class="stat-item">
            å…± <strong>{{ totalCount }}</strong> ä¸ªæ¡ˆä¾‹
          </span>
          <span class="stat-item" v-if="activeTag || searchKeyword">
            å½“å‰æ˜¾ç¤º <strong>{{ filteredCases.length }}</strong> ä¸ª
          </span>
        </div>

        <!-- æ“ä½œæŒ‰é’®ç»„ -->
        <div class="action-btns">
          <!-- éšæœºæŒ‰é’® -->
          <span class="action-btn" @click="shuffleCases" title="éšæœºæ’åº">
            ğŸ² éšæœº
          </span>
          <!-- æ’åºæŒ‰é’® -->
          <span class="action-btn" @click="toggleSort" title="åˆ‡æ¢æ’åº">
            {{ sortOrder === "asc" ? "â¬‡ æ­£åº" : "â¬† å€’åº" }}
          </span>
        </div>
      </div>

      <!-- æ ‡ç­¾ç­›é€‰å™¨ -->
      <div class="filter-bar">
        <div class="filter-wrap">
          <span
            class="filter-tag"
            :class="{ active: activeTag === '' }"
            @click="activeTag = ''"
          >
            å…¨éƒ¨
          </span>
          <span
            class="filter-tag"
            v-for="tag in tagStats"
            :key="tag.name"
            :class="{ active: activeTag === tag.name }"
            @click="activeTag = tag.name"
          >
            {{ tag.name }}
            <span class="tag-count">{{ tag.count }}</span>
          </span>
        </div>
      </div>

      <!-- æ¡ˆä¾‹åˆ—è¡¨ -->
      <transition-group name="case-list" tag="div" class="wrap flex-xb">
        <a
          class="ev"
          v-for="ev in filteredCases"
          :key="ev.name"
          :href="'http://' + ev.link"
          target="_blank"
        >
          <div class="group">
            <div class="state flex-yc"><i></i><i></i><i></i></div>
            <div class="pic flex-xc-yc">
              <img v-lazy="ev.img" alt="" />
              <div class="overlay" v-if="ev.description">
                <p class="desc">{{ ev.description }}</p>
              </div>
            </div>
          </div>
          <div class="info">
            <div class="text">{{ ev.name }}</div>
            <div class="tags" v-if="ev.tags && ev.tags.length">
              <span class="tag" v-for="tag in ev.tags" :key="tag">
                {{ tag }}
              </span>
            </div>
          </div>
        </a>

        <div class="ev placeholder" key="placeholder1"></div>
        <div class="ev placeholder" key="placeholder2"></div>
      </transition-group>

      <!-- æ— ç»“æœæç¤º -->
      <div class="no-result" v-if="filteredCases.length === 0">
        <p>ğŸ˜¢ æ²¡æœ‰æ‰¾åˆ°åŒ¹é…çš„æ¡ˆä¾‹</p>
        <span class="reset-btn" @click="resetFilters">é‡ç½®ç­›é€‰</span>
      </div>
    </div>
  </div>
</template>

<script>
import "@/assets/css/case.css";
import caseList from "@/data/cases.js";

export default {
  name: "CasePage",
  data() {
    return {
      caseList: [...caseList],
      activeTag: "",
      sortOrder: "asc",
      searchKeyword: "",
      isShuffled: false,
    };
  },
  computed: {
    totalCount() {
      return caseList.length;
    },
    tagStats() {
      const tagMap = new Map();
      caseList.forEach((item) => {
        if (item.tags) {
          item.tags.forEach((tag) => {
            tagMap.set(tag, (tagMap.get(tag) || 0) + 1);
          });
        }
      });
      return Array.from(tagMap.entries())
        .map(([name, count]) => ({ name, count }))
        .sort((a, b) => a.name.localeCompare(b.name));
    },
    filteredCases() {
      let result = this.caseList;

      // æœç´¢è¿‡æ»¤
      if (this.searchKeyword.trim()) {
        const keyword = this.searchKeyword.toLowerCase().trim();
        result = result.filter(
          (item) =>
            item.name.toLowerCase().includes(keyword) ||
            (item.description &&
              item.description.toLowerCase().includes(keyword))
        );
      }

      // æ ‡ç­¾ç­›é€‰
      if (this.activeTag) {
        result = result.filter(
          (item) => item.tags && item.tags.includes(this.activeTag)
        );
      }

      // æ’åºï¼ˆééšæœºçŠ¶æ€ä¸‹ï¼‰
      if (!this.isShuffled && this.sortOrder === "desc") {
        return [...result].reverse();
      }

      return result;
    },
  },
  created() {},
  methods: {
    toggleSort() {
      this.isShuffled = false;
      this.caseList = [...caseList];
      this.sortOrder = this.sortOrder === "asc" ? "desc" : "asc";
    },
    shuffleCases() {
      this.isShuffled = true;
      const shuffled = [...caseList];
      for (let i = shuffled.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]];
      }
      this.caseList = shuffled;
    },
    resetFilters() {
      this.activeTag = "";
      this.searchKeyword = "";
      this.sortOrder = "asc";
      this.isShuffled = false;
      this.caseList = [...caseList];
    },
  },
};
</script>

